* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-size: 16px;
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  text-align: center;
  /* flex-wrap: wrap; */
  /* justify-content: space-around; */
  height: 80px;
  background: linear-gradient(90deg, #223080 1.18%, #3726d3 100%);
  color: whitesmoke;
  padding: 10px;
}
.meme-logo {
  height: 100%;
  margin-right: 2%;
}

.header h2 {
  font-size: 1.5rem;
  margin-right: auto;
}
.header h5 {
  font-size: 1.2rem;
  font-weight: 500;
  /* margin-right: 2%; */
}
main {
  padding: 10px 1px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.form {
  max-width: 800px;

  width: 100%;
  display: grid;
  grid-template: 1.5rem 1.5rem / 1fr 1fr;
  gap: 30px;
  /* overflow: hidden; */
  margin: 10px auto;
}
.form-input {
  /* margin: 1rem 0; */
  width: 95%;
  border: 1px solid #444;
  border-radius: 5px;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 1rem;
  text-indent: 8px;
  height: 2rem;
}

.form-button {
  grid-column: 1 /3;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: 1rem;
  background: linear-gradient(40deg, #3726d3 30%, #462854 30%, #223080 70%);
  color: whitesmoke;
  border: none;
  overflow: hidden;
  height: 3rem;
  /* margin: 2rem 0; */
}
.meme-text {
  position: absolute;
  max-width: calc(20ch);
  overflow-wrap: break-word;
  text-align: center;
  left: 40%;
  transform: translateX(-50%);
  margin: 30px 0;
  padding: 0 5px;
  font-family: impact, sans-serif;
  font-size: 2rem;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}
.bottom {
  bottom: 30px;
}

.top {
  top: 10px;
}
.img-box {
  max-width: 800px;
  width: 100%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.meme {
  position: relative;
}
.meme-img {
  width: 100%;
  min-width: 35%;
  margin-top: 3rem;
}

@media screen and (min-device-width: 300px) and (max-device-width: 700px) {
  body {
    font-size: 15px;
  }

  .meme-img {
    width: 100%;
    /* height: 30%; */
    padding: 20px;
    margin: 0 auto;
  }
  .meme-text {
    font-size: 1em;
  }
}
